// Color variables (appears count calculates by raw css)
@color0: #95989a; // Appears 6 times
@color1: #969696; // Appears 6 times
@color2: #cccccc; // Appears 5 times
@color3: #6cd1ea; // Appears 5 times
@color4: #a5a7a9; // Appears 3 times
@color5: #d53635; // Appears 2 times
@color6: #3b5998; // Appears 2 times
@color7: #7bb32e; // Appears 2 times
@color8: #2f2f2f; // Appears 2 times

// Width variables (appears count calculates by raw css)
@width5: 100%; // Appears 6 times

// Height variables (appears count calculates by raw css)
@height2: 50px; // Appears 2 times

.top-section {
	background-color: #fafafa;
	font-size: 12px;
	.breadcrumb {
		background-color: transparent;
		margin-bottom: 2px;
                color:#5F5F5F;
		li {
			display: inline-block;
		}
                a{
                    margin:7px;
                    text-decoration: none;
                }

	}
	.breadcrumb>li+li {
		&::before {
			color: #4d4d4d;
			content: "| ";
		}
	}
}
#arctile-section {
	.title {
		.text {
			background-repeat: no-repeat;
			background-size: 100% 100%;
			color: white;
			font-family: 'pompiere_regular';
			font-size: 20px;
			height: 45px;
			line-height: 58px;
			margin-left: auto;
			margin-right: auto;
			margin-top: 25px;
			position: relative;
			text-align: center;
			width: 287px;
		}
	}
	.item-articles {
		margin-bottom: 50px;
		.first-item {
			margin-top: 15px;
			padding-left: 23px;
			padding-right: 23px;
			img {
				border-radius: 5px;
				max-height: 235px;
				width: @width5;
			}
			.wrapper-first {
				background-color: white;
				bottom: 0px;
				height: 48px;
				opacity: 0.6;
				padding-left: 15px;
				position: absolute;
				width: @width5;
				div {
					line-height: 16px;
					padding: 5px;
					width: 80%;
				}
			}
		}
		.article_grid {
			margin:auto;
			.wrapper-masonry {
				padding: 8px;
				.shadow {
					-moz-box-shadow: 0px 2px 4px 1px @color1;
					-webkit-box-shadow: 0px 2px 4px 1px @color1;
					border-radius: 5px;
					box-shadow: 0px 2px 4px 1px @color1;
				}
				img {
					border-radius: 5px 5px 0px 0px;
					min-height: 130px;
					width:@width5;
				}
				.description {
					color: #9fa2a4;
					font-size: 12px;
					padding: 5px;
				}
				.title-item {
					color: @color8;
					padding-top: 5px;
					.underline_articles {
						background-color: @color8;
						height: 2px;
						margin-bottom: 5px;
						margin-top: 5px;
						width: 45px;
					}
				}
			}
		}
	}
}
.arctile-detail-section {
	.social-mobile {
                text-align:center;
		.comment {
			.text {
				font-size: 15px;
				position: relative;
				top: -8px;
			}
		}
		.fb {
			i {
				color: @color6;
			}
		}
		.wechat {
			i {
				color: @color7;
			}
		}
		.heart {
			i {
				color: red;
			}
		}
		.btn {
			border-radius: 0px;
			font-size: 25px;
			padding: 5px 10px 5px 10px;
		}
	}
	.right-side {
		left: 20px;
	}
	.hover_social {
		font-size: 25px;
		margin-left: -55px;
		position: fixed;
		top: 10%;
		z-index: 650;
	}
	.static_social {
		font-size: 25px;
		margin-left: -55px;
		position: absolute;
		z-index: 550;
	}
	.hover_social_m {
		bottom: 0;
		position: fixed;
		z-index: 500;
          
	}
	.social-network {
		.popover {
			width: 170px;
		}
		div {
			padding: 2px;
			width: 40px;
		}
		.comment {
			background-color: white;
			border: 1px solid @color2;
			font-size: 15px;
			margin-bottom: 10px;
			text-align: center;
			span {
				font-size: 25px;
			}
		}
		.soc {
			background-color: white;
			border-left: 1px solid @color2;
			border-right: 1px solid @color2;
			border-top: 1px solid @color2;
		}
		.wechat {
			font-size: 22px;
			padding-left: 5px;
			i {
				color: @color7;
			}
			a {
				&:hover {
					border-color: transparent;
					outline: none;
				}
				&:focus {
					border-color: transparent;
					outline: none;
				}
			}
		}
		.fb {
			padding-left: 7px;
			i {
				color: @color6;
			}
		}
		.share {
			padding-left: 7px;
			a {
				&:hover {
					border: none!important;
				}
				&:focus {
					border: none!important;
				}
			}
		}
		.heart {
			background-color: white;
			border: 1px solid @color2;
			text-align: center;
			i {
				color: red;
			}
		}
	}
	.advertising_center {
		padding: 0px;
	}

	.sub-title {
		border-bottom: 0.3px solid @color0;
		margin-top: 15px;
		padding-bottom: 30px;
		img {
			border: 0.3px solid @color0;
			height: 43px;
			margin-right: 5px;
			width: 43px;
		}
		span {
			color: @color5;
		}
	}
	.img-content {
		margin-top: 15px;
	}
	.description-title {
		color: @color5;
		font-size: 20px;
		margin-bottom: 15px;
		margin-top: 15px;
	}
	.tag-section {
		border-bottom: 1px solid @color0;
		border-top: 1px solid @color0;
		color: @color4;
                padding-left: 0px;
		.title-tag {
			font-size: 20px;
			line-height: 2em;
                            width: 77px;
		}
		.tag-list {
			padding-bottom: 10px;
			padding-top: 10px;
                            padding-left: 0px;
			.tab-btn-content {
				margin-bottom: 5px;
                                padding-left:0px;
			}
			a {
				-moz-box-shadow: 1px 1px 2px -1px @color1;
				-webkit-box-shadow: 1px 1px 2px -1px @color1;
				border-radius: 3px;
				border: 1px solid @color0;
				box-shadow: 1px 1px 2px -1px @color1;
				color: @color4;
				font-size: 12px;
				padding: 3px;
			}
		}
	}
	.description {
		color: #5F5F5F;
                img{
                    margin:auto;
                    display:table;
                }
	}
	.divider-articles {
		background-color: white;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		height: @height2;
		margin-bottom: -7px;
		margin-left: -100%;
		margin-top: 20px;
		padding-top: 7px;
		position: relative;
		right: -57px;
		.wrapper-details {
			margin-left: 49%;
			a {
				color: white;
				font-family: 'pompiere_regular';
				font-size: 25px;
			}
		}
	}
	.related-content {
		margin-top: 20px;
		padding: 0px;
		.related-item {
			margin-bottom: 15px;
			padding: 0px;
			.img-related {
				img {
					width: @width5;
				}
			}
			.title-related {
				font-size: 12px;
				padding-top: 5px;
			}
		}
	}
	.most-section {
		border-top: 0.5px solid @color3;
		margin-top: 25px;
		padding: 0px;
		.tab-page {
			display: none;
		}
		.active-page {
			display: block;
		}
		.title {
			border-bottom: 0.5px solid @color3;
			font-family: 'pompiere_regular';
		
			font-size: 30px;
			margin: 0px;
                        font-weight: bold;
			padding-top: 5px;
			text-align: center;
		}
		ul {
			list-style: none;
			text-align: center;
			li {
				cursor: hand;
				cursor: pointer;
			}
			li.active {
				color: @color3;
			}
			li.tab-one {
				float: left;
			}
			li.tab-two {
				border-left: 1px solid @color3;
				float: right;
				padding-left:10px;
				width: 55%;
			}
		}
		.most-list {
			
			margin: 0px;
			padding: 0px;
                        margin-top: 10px;
			.item-most {
                                margin: 0px;
				padding: 0px;
				margin-bottom: 15px;
                                    margin-top: 5px;
   

				.img-most {
					margin: 0px;
					padding: 0px;
					img {
						width: @width5;
					}
				}
				.right-most {
                                        padding: 0px;
					padding-left: 5px;
					
					.date {
						font-size: 12px;
					}
					.description {
						color: @color3;
						font-size: 13px;
					}
				}
			}
		}
	}
}
.details-articles, .details-articles div, .sub-title, .title  {
	padding-left: 0px;
	padding-right: 0px;
}
.details-articles {
	.img-content {
		img {
			margin:auto;
		}
	}
}
